#header-major {
  /* 1200px==============================*/
  /* 992px==============================*/
  /* 768px==============================*/
  /* 576px==============================*/
  /* 400px==============================*/
}
@media (max-width: 1200px) {
  #header-major {
    /* изменение разрешения картинки и высоты header */
    background-image: url("../../build/img/header-bc-image1200x712.png");
    height: 712px;
    /* изменение высоты псевдоэлемента с затемнением фона */
    /* изменение отступа главного заголовка сверху header */
  }
  #header-major::before {
    height: 712px;
  }
  #header-major .header-major__content {
    top: 170px;
  }
}
@media (max-width: 992px) {
  #header-major {
    /* изменение разрешения картинки и высоты header */
    background-image: url("../../build/img/header-bc-image992x588.png");
    height: 588px;
    /* изменение высоты псевдоэлемента с затемнением фона */
    /* изменение размера шрифта в меню навигации */
    /* изменение отступа главного заголовка сверху header */
  }
  #header-major::before {
    height: 588px;
  }
  #header-major .header-major__menu__nav {
    font-size: 10px;
  }
  #header-major .header-major__content {
    top: 140px;
  }
}
@media (max-width: 768px) {
  #header-major {
    /* изменение разрешения картинки и высоты header */
    background-image: url("../../build/img/header-bc-image768x455.png");
    height: 455px;
    /* изменение высоты псевдоэлемента с затемнением фона */
    /* изменение высоты menu, закрепление fixed и заливка фоновым цветом */
    /* изменение отступа главного заголовка сверху header */
  }
  #header-major::before {
    height: 455px;
  }
  #header-major .header-major__menu {
    justify-content: space-between;
    height: 50px;
    position: fixed;
    background-color: #161418;
    /* изменение параметров отступа логотипа */
    /* вместо  nav появляется меню бургер */
    /* меню nav становиться всплывающем при активном бургер */
  }
  #header-major .header-major__menu__logo {
    margin: 5px 0 0 0;
  }
  #header-major .header-major__menu__burger {
    display: block;
    position: relative;
    height: 21px;
    width: 30px;
    margin: 15px 35px 0 0;
  }
  #header-major .header-major__menu__burger__span {
    position: absolute;
    background-color: #fa7c30;
    width: 100%;
    height: 3px;
    left: 0;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  #header-major .header-major__menu__burger__span.active {
    transform: scale(0);
  }
  #header-major .header-major__menu__burger::before,
  #header-major .header-major__menu__burger::after {
    content: "";
    background-color: #fa7c30;
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  #header-major .header-major__menu__burger::before {
    top: 0;
  }
  #header-major .header-major__menu__burger::after {
    bottom: 0;
  }
  #header-major .header-major__menu__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
  }
  #header-major .header-major__menu__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  #header-major .header-major__menu__nav {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    margin: 50px 0 0 0;
    padding: 0;
    height: 100vh;
    top: -100vh;
    width: 100%;
    overflow: auto;
    background-color: #161418;
    font-size: 18px;
    transition: all 0.3s ease 0s;
  }
  #header-major .header-major__menu__nav__ul-left,
  #header-major .header-major__menu__nav__ul-right {
    flex-direction: column;
    flex-grow: 0;
  }
  #header-major .header-major__menu__nav__ul-left {
    flex-grow: 0;
  }
  #header-major .header-major__menu__nav.active {
    top: 0;
  }
  #header-major .header-major__content {
    top: 100px;
  }
}
@media (max-width: 576px) {
  #header-major {
    /* изменение разрешения картинки и высоты header */
    background-image: url("../../build/img/header-bc-image576x341.png");
    height: 341px;
    /* изменение высоты псевдоэлемента с затемнением фона */
    /* изменение отступа главного заголовка сверху header
            и изменение отступа кнопки header */
  }
  #header-major::before {
    height: 341px;
  }
  #header-major .header-major__content {
    top: 60px;
    text-align: center;
  }
  #header-major .header-major__content__button {
    margin: auto;
  }
}
@media (max-width: 400px) {
  #header-major {
    /* изменение разрешения картинки и высоты header */
    background-image: url("../../build/img/header-bc-image400x236.png");
