#header-major {
  display: block;
  position: relative;
  width: 100%;
  max-width: 1600px;
  min-width: 320px;
  height: 950px;
  background-image: url("../../build/img/header-bc-image1600x950.png");
  background-color: #a8aaac;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 0;
  margin: 0;
}
#header-major::before {
  content: "";
  position: absolute;
  width: 100%;
  max-width: 1600px;
  height: 950px;
  top: 0;
  left: 0;
  background-color: rgba(11, 12, 14, 0.5);
}
#header-major .header-major__menu {
  display: flex;
  width: 100%;
  height: 80px;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
#header-major .header-major__menu__burger {
  display: none;
}
#header-major .header-major__menu__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 39px;
  text-decoration: none;
  color: #ffffff;
  padding: 0 15px 0 35px;
  margin: 20px 0 0 0;
  z-index: 2;
}
#header-major .header-major__menu__logo h5 {
  margin: 0;
  padding: 0;
}
#header-major .header-major__menu__logo h5 span {
  color: #fa7c30;
  font-weight: bold;
  font-size: inherit;
  margin: 0 0 0 -5px;
  padding: 0;
}
#header-major .header-major__menu__nav {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  margin: 20px 0 0 0;
  padding: 0 35px 0 0;
  height: 39px;
  flex-grow: 1;
}
